import React, { useEffect, useState } from 'react';
import {
  fetchPriceItems,
  checkListNameExists,
  saveSupplyListMeta,
  saveSupplyListItems
} from '../../tables/tables';
import Layout from '../../components/Layout';

export default function ListBuilder() {
  const [items, setItems] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [listName, setListName] = useState('');
  const [desiredMargin, setDesiredMargin] = useState(40);
  const [shippingRate, setShippingRate] = useState(17);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [marginDialogOpen, setMarginDialogOpen] = useState(false);
  const [marginInput, setMarginInput] = useState(30);

  useEffect(() => {
    fetchPriceItems().then(data => {
      const initialized = data.map(item => {
        const costEa = item.bulk / item.carton;
        return {
          ...item,
          selected: false,
          qty: 1,
          sell: parseFloat(costEa.toFixed(2))
        };
      });
      setItems(initialized);
      setLoading(false);
    });
  }, []);

  const updateItem = (index: number, field: string, value: any) => {
    const updated = [...items];
    if (field === 'qty') {
      updated[index][field] = parseInt(value) || 1;
    } else if (field === 'sell') {
      updated[index][field] = parseFloat(value) || 0;
    } else {
      updated[index][field] = value;
    }
    setItems(updated);
  };

  const applyMarginToAll = () => {
    const updated = items.map(item => {
      if (!item.selected) return item;
      const costEa = item.bulk / item.carton;
      const newSell = costEa / (1 - marginInput / 100);
      return {
        ...item,
        sell: parseFloat(newSell.toFixed(2))
      };
    });
    setItems(updated);
    setMarginDialogOpen(false);
  };

  const filtered = items.filter(row => row.selected);
  const totalCost = filtered.reduce((sum, row) => sum + (row.bulk / row.carton) * row.qty, 0);
  const totalSell = filtered.reduce((sum, row) => sum + row.sell * row.qty, 0);
  const totalProfit = filtered.reduce((sum, row) => sum + (row.sell - (row.bulk / row.carton)) * row.qty, 0);
  const roundedProfit = Math.abs(totalProfit) < 0.005 ? 0 : totalProfit;
  const actualMargin = totalSell > 0 ? (roundedProfit / totalSell) * 100 : 0;
  const finalCost = totalSell + shippingRate;

  const handleSave = async () => {
    setSaving(true);
    setError('');
    setSuccess('');

    if (!listName.trim()) {
      setError('List name is required.');
      setSaving(false);
      return;
    }

    const exists = await checkListNameExists(listName);
    if (exists) {
      console.warn(`‚ö†Ô∏è Overwriting existing list: ${listName}`);
    }

    const metaSaved = await saveSupplyListMeta(listName, desiredMargin, shippingRate);

    const validItems = items.filter(item => item.selected && item.uid);

    const itemsSaved = await saveSupplyListItems(listName, validItems, true);

    if (metaSaved && itemsSaved) {
      setSuccess('‚úÖ List and items saved successfully!');
    } else {
      setError('‚ùå Failed to save list or items.');
    }

    setSaving(false);
  };

  if (loading) return <div style={{ padding: 20 }}>Loading...</div>;

  return (
    <Layout>
      <div style={{ padding: 20, fontFamily: 'Arial' }}>
        <h2>Build New Supply List</h2>

        <div style={{ marginBottom: 10 }}>
          <label><strong>List Name: </strong></label>
          <input value={listName} onChange={e => setListName(e.target.value)} style={{ marginLeft: 10 }} />
        </div>

        <table border={1} cellPadding={6} cellSpacing={0}>
          <thead>
            <tr>
              <th>Add</th>
              <th>Item</th>
              <th>SKU</th>
              <th>Cost Ea.</th>
              <th>Qty</th>
              <th>Sell</th>
              <th>Profit</th>
            </tr>
          </thead>
          <tbody>
            {items.map((row, idx) => {
              const costEa = row.bulk / row.carton;
              let profit = (row.sell - costEa) * row.qty;
              if (Math.abs(profit) < 0.005) profit = 0;
              return (
                <tr key={row.sku}>
                  <td>
                    <input
                      type="checkbox"
                      checked={row.selected}
                      onChange={e => updateItem(idx, 'selected', e.target.checked)}
                    />
                  </td>
                  <td>{row.item}</td>
                  <td>{row.sku}</td>
                  <td>${costEa.toFixed(2)}</td>
                  <td>
                    <input
                      type="number"
                      value={row.qty}
                      onChange={e => updateItem(idx, 'qty', e.target.value)}
                      disabled={!row.selected}
                      style={{ width: 60 }}
                    />
                  </td>
                  <td>
                    <input
                      type="number"
                      value={row.sell}
                      onChange={e => updateItem(idx, 'sell', e.target.value)}
                      disabled={!row.selected}
                      style={{ width: 60 }}
                    />
                  </td>
                  <td>${profit.toFixed(2)}</td>
                </tr>
              );
            })}
          </tbody>
        </table>

        <div style={{ marginTop: 20 }}>
          <button onClick={() => setMarginDialogOpen(true)} style={{ padding: '6px 12px', marginBottom: 10 }}>
            üìà Set All to X% Profit Margin
          </button>

          {marginDialogOpen && (
            <div style={{ padding: 10, background: '#eee', marginBottom: 20, border: '1px solid #aaa' }}>
              <label><strong>Profit Margin (%): </strong></label>
              <input
                type="number"
                value={marginInput}
                onChange={e => setMarginInput(parseFloat(e.target.value) || 0)}
                style={{ width: 60, marginLeft: 10 }}
              />
              <button onClick={applyMarginToAll} style={{ marginLeft: 10, padding: '4px 12px' }}>Apply</button>
              <button onClick={() => setMarginDialogOpen(false)} style={{ marginLeft: 10 }}>Cancel</button>
            </div>
          )}

          <p>
            <label><strong>Shipping Rate ($):</strong></label>
            <input
              type="number"
              value={shippingRate}
              onChange={e => setShippingRate(parseFloat(e.target.value) || 0)}
              style={{ marginLeft: 10, width: 60 }}
            />
          </p>

          <hr />
          <p><strong>Total Cost:</strong> ${totalCost.toFixed(2)}</p>
          <p><strong>Total Profit:</strong> ${totalProfit.toFixed(2)}</p>
          <p><strong>Profit Margin:</strong> {actualMargin.toFixed(2)}%</p>
          <p><strong>Final Cost to Customer (with Shipping):</strong> ${finalCost.toFixed(2)}</p>

          <button onClick={handleSave} disabled={saving} style={{ marginTop: 20, padding: '8px 16px' }}>
            {saving ? 'Saving...' : 'Save List'}
          </button>
          {error && <p style={{ color: 'red' }}>{error}</p>}
          {success && <p style={{ color: 'green' }}>{success}</p>}
        </div>
      </div>
    </Layout>
  );
}
